.App {
  position: relative;
  min-width: 553px;
  font-size: 16px;
}
.clearfix::before{
  content: '';
  display: block;
  clear: both;
}
.App aside{
  position: fixed;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  -webkit-box-shadow: 3px 0px 5px #ccc9c9;
          box-shadow: 3px 0px 5px #ccc9c9;
  background: #1f2442;
}
.App aside>header{
  height: 4.5rem;
  width: 100%; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background: #70f1e5;
  color: #fff;
  padding: 0 1em;
}
.App .content{
  margin-left: 200px;
  height: 100%;
  width: 70%;
}
.App .content>header{
  width: 100%;
  height: 4.5rem;
  line-height: 4.5rem;
  padding: 0 1.2em;
  background: #fff;
  -webkit-box-shadow: -4px 2px 5px #ccc9c9;
          box-shadow: -4px 2px 5px #ccc9c9;
}
.App .content>.todoList> li{
  list-style: none;
  margin-bottom: 20px;
}
.App .content .todoList-ct{
  height: 100%;
  padding: 1.2rem 0.2rem;
}
.App .content .todoList{
  width: 100%;
  border-radius: 3px;
}
.App .iconSignOut{
  cursor: pointer;
  position: absolute;
  right: 24px;
}


